<template> 
    <div class="generator-after">
        <p class="generator-head" v-on:click="isActive.flag=!isActive.flag,toggleButton(isActive)">アニメーション後</p><button class="button-dropdown" v-on:click="isActive.flag=!isActive.flag,toggleButton(isActive)">{{isActive.icon}}</button>
        <div class="generator-section" v-bind:class="{active:isActive.flag}">
            <css ref="getCSS"></css>
            <transform></transform>
            <setting></setting>
        </div>
    </div>
</template>

<script>
import css from './option/css.vue'
import transform from './option/transform.vue'
import setting from './option/setting.vue'


export default{
    components: {
        css,
        transform,
        setting
    },
    data() {
      return {
        isActive: {
            flag: false,
            icon: "▼"
        },
        css: {
        },
        transform: {
        },
        setting: {
        }
      }
    },
    watch: {
        css: {
            handler:function(){
                this.$parent.after.css = this.css;
            },
            deep: true
        },
        transform: {
            handler:function(){
                console.log(this.transform)
                this.$parent.after.transform = this.transform;
            },
            deep: true
        },
        setting: {
            handler:function(){
                this.$parent.after.setting = this.setting;
            },
            deep: true
        }
    },
    methods: {
      toggleButton: function (icon) {
          console.log(icon)
          if(icon.flag === true){
            icon.icon = "▶︎"
          }else{
            icon.icon = "▼"
          }
      }
    },
  }

</script>

<style lang="scss">
    .generator-after{
        height: auto;
        margin: 15px;
        box-sizing: border-box;
        border: 2px solid #000;
        position: relative;
        background: #ffd5d5;
        .generator-head{
            position: relative;
            font-size: 23px;
            padding: 15px;
            cursor: pointer;
        }
        .button-dropdown{
            display: inline-block;
            // text-align: right;
            position: absolute;
            top: 10px;
            right: 10px; 
            border: none;
            background: unset;
            font-size: 26px;
            outline: unset;
        }
        .generator-section{
            height: 700px;
            padding: 0 15px 15px 15px;
            display: block;
            overflow-y: scroll;
        }
        .active{
            display: none !important;
        }
    }

</style>